import * as React from 'react';
import { Classes, Callout } from '@blueprintjs/core';
import classnames from 'classnames';
import { css, StyleSheet } from 'aphrodite';

const styles = StyleSheet.create({
  container: {
    fontSize: '1.1em',
  },
  table: {
    margin: '20px 0',
  },
  callout: {
    margin: '20px 0',
  },
});

export interface InfoPaneProps {
  className?: string;
}

export const InfoPane = (props: InfoPaneProps) => {
  const { className } = props;

  const tableCalsses = classnames(
    Classes.HTML_TABLE,
    Classes.HTML_TABLE_BORDERED,
    Classes.HTML_TABLE_STRIPED,
    css(styles.table),
  );

  return (
    <div className={ classnames(className, css(styles.container)) }>
      <h2>What's this ...</h2>
      <p>
      This is a flash cards app ( yes, another one ). The intention was to made a very basic
app for very specific use case for personal usage. But if someone finds this app usefull, that would be great.
      </p>
      <p>
      The primary use case for this app is creating flashcards from sheets loaded from
google translate app ( did you know that you can download the words you starred? ).
But actually you can load here any sheets that are in similar format as google translate's one ( anything you might want to memorize, write it into google
sheets, then download as csv file and load into the app and you can train your memorization ).
      </p>
      <h3>How to use it</h3>
      <ol>
        <li>create a card set</li>
        <li>fill the card set with data</li>
        <li>start exercise</li>
      </ol>
      <p>
      Currentyle the app expects the following format:
      </p>
      <table className={ tableCalsses }>
        <thead>
          <tr>
            <th>label A</th><th>label B</th><th>text A</th><th>text A</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>english</td><td>русский</td><td>city</td><td>город</td>
          </tr>
          <tr>
            <td>...</td><td>...</td><td>...</td><td>...</td>
          </tr>
        </tbody>
      </table>
      <Callout intent='warning' className={ css(styles.callout) }>
      Only one file format is supported yet. It is a comma separated values ( CSV ) format.
      </Callout>
      <p>
      We don't have user accaunts here. All the data stored on your device locally. This means that you can't share progress between, let's say, your mobile phone and
descktop computer ( but you can load the same data on both devices ). This is because I don't wont to pay for servers.
      </p>
      <p>
        If you find a bug or have a suggestion, please write to me at <a href='mailto:arfedulov@gmail.com'>arfedulov@gmail.com</a>.
      </p>
    </div>
  );
};
